<template>
  <section class="main" sticky-container>
    <loader class="main__loader" v-if="!isLoaded"></loader>
    <template v-else>
      <div class="main__content main__content_a">
        <back-link
          v-if="!isStandaloneApp"
          programmatic
          :routes="['home', 'my-installation-history', 'my-apps']"
          class="back-link"
        >
          {{ $t('GO_BACK') }}
        </back-link>

        <div class="banners" v-if="isStandaloneApp">
          <div class="banner">
            <p v-html="$t('STANDALONE_APP_NOTICE', {href: `https://${BRAND_CONFIG.domain}`})"></p>
          </div>
        </div>
        <div class="banners" v-else-if="installationFailureReason || getDownloadFailureReason(ug)">
          <div v-if="installationFailureReason" class="banner">
            <html-with-links tag="p" :html="installationFailureReason" />
          </div>

          <div v-if="getDownloadFailureReason(ug)" class="banner">
            <html-with-links tag="p" :html="getDownloadFailureReason(ug)" />
          </div>
        </div>

        <div class="section heading">
          <img
            v-if="hasIcon"
            class="heading__icon"
            :src="ug.object.icon_uri"
            @error="iconFailedToLoad = true"
          />
          <div v-else class="heading__icon-stub"></div>
          <div
            ref="desc"
            class="heading__desc"
            :class="{
              heading__desc_expanded: isDescExpanded || editMode,
            }"
          >
            <div class="heading__mobile">
              <img
                v-if="hasIcon"
                class="heading__icon-mobile"
                :src="ug.object.icon_uri"
              />
              <div v-else class="heading__icon-stub-mobile"></div>
              <div>
                <h1 class="heading__title">
                  <editable-line v-model="ug.object.name" :placeholder="$t('APP_NAME')"
                                 field="name"/>
                  <span class="heading__badge" v-if="ug.object.type === 'tweak'">
                    {{ $t('TWEAKED') }}
                  </span>
                  <span class="heading__badge heading__badge_jit" v-if="ug.object.use_jit_support">
                    <sup>JIT</sup>
                  </span>
                </h1>
                <p
                  v-if="!editMode && ug.object.lead"
                  class="short-description"
                >
                  {{ ug.object.lead }}
                </p>
              </div>
            </div>

            <template v-if="editMode">
              <h3 class="h3">{{ $t('SHORT_DESCRIPTION') }}</h3>
              <v-input class="short-description-input" v-model="ug.object.lead"/>
              <v-textarea class="short-description-textarea" rows="2" resize="none"
                          v-model="ug.object.lead"/>
              <h3 class="h3">{{ $t('DESCRIPTION') }}</h3>
            </template>
            <div ref="desc__inner">
              <editable-markdown class="heading__text" v-model="ug.object.description"
                                 field="description"/>
            </div>
            <a href="#" class="more" @click.prevent="isDescExpanded = true">{{
                $t('VIEW MORE')
              }}</a>
          </div>
        </div>
        <div class="section section_pt" v-if="areScreenshotsVisible || editMode">
          <h2 class="h2">
            {{ $t('SCREENSHOTS') }}
            <ul class="select">
              <li
                v-for="os in screenshotsOSs"
                class="select__tab"
                :class="{ select__tab_active: screenshotsOS === os}"
                @click="screenshotsOS = os"
                :key="os"
              >
                {{ beautifyDevice(os) }}
              </li>
            </ul>
          </h2>

          <v-button
            v-if="editMode"
            type="primary"
            :loading="uploadingScreenshots"
            @click.native="openScreenshotsFileDialog"
          >
            {{ $t('ADD_SCREENSHOTS') }}
          </v-button>

          <v-draggable
            v-for="(array, os) in ug.object.screenshots_uris_by_os_type"
            v-model="ug.object.screenshots_uris_by_os_type[os]"
            :disabled="!editMode"
            :key="os"
            v-show="screenshotsOS === os && array.length"
          >
            <transition-group tag="ul" class="screenshots">
              <li
                class="screenshots__item"
                :class="{
                  screenshots__item_draggable: editMode
                }"
                v-for="(screenshot, idx) in array"
                :key="screenshot"
              >
                <img class="screenshots__img" :src="screenshot"/>

                <button
                  v-if="editMode"
                  class="screenshots__remove-button"
                  @click="removeScreenshot(os, idx)"
                >
                  <img src="@/assets/icon-x.svg"/>
                </button>
              </li>
            </transition-group>
          </v-draggable>
        </div>
      </div>
      <div class="main__content main__content_b main__content_ptop">
        <div v-if="ug.object.whatsnew || editMode" class="section">
          <h2 class="h2">{{ $t('WHATSNEW') }}</h2>
          <div
            v-if="ug.object.whatsnew || editMode"
            ref="changelog"
            class="changelog"
            :class="{ changelog_expanded: isChangelogExpanded || editMode }"
          >
            <editable-markdown ref="changelog__inner" v-model="ug.object.whatsnew"
                               field="whatsnew"/>
            <a
              href="#"
              class="more"
              @click.prevent="isChangelogExpanded = true"
            >{{ $t('VIEW MORE') }}</a
            >
          </div>
        </div>
        <div class="section section_pt section_noborder">
          <h2 class="h2 h2_badge">
            {{ $t('INFORMATION') }}
          </h2>
          <div class="info">
            <table class="info__list">
              <v-detail v-if="editMode" :label="$t('PUBLIC')" v-model="ug.object.is_public"
                        field="is_public" editable type="binary"/>
              <v-detail v-if="editMode" :label="$t('USE_JIT_SUPPORT')"
                        v-model="ug.object.use_jit_support" field="use_jit_support"
                        editable type="binary"/>
              <v-detail v-if="editMode" :label="$t('USE_SIDELOADING_PATCH')"
                        v-model="ug.object.use_sideloading_patch" field="use_sideloading_patch"
                        editable type="binary"/>
              
              <v-detail v-if="editMode" :label="$t('USE_EXPLICIT_BUNDLE_ID')"
                        v-model="ug.object.use_explicit_bundle_id" field="use_explicit_bundle_id"
                        editable type="binary"/>
              <v-detail v-if="editMode" :label="$t('USE_GROUP_PATCH')"
                        v-model="ug.object.use_group_patch" field="use_group_patch" editable
                        type="binary"/>

              <v-detail v-if="editMode" label="18+" v-model="ug.object.is_18plus" field="is_18plus"
                        editable type="binary"/>

              <v-detail :label="$t('DEVELOPER')" v-model="ug.object.developer_name"
                        field="developer_name" editable filterable/>
              <v-detail :label="$t('WEBSITE')" v-model="ug.object.website_uri" field="website_uri"
                        editable>
                <a class="info__link" :href="ug.object.website_uri" target="_blank">
                  {{ $t('VISIT') }}
                </a>
              </v-detail>

              <v-detail :label="$t('PRICE')" :value="ug.object.price_cents">
                {{ (ug.object.price_cents / 100).toFixed(2) }}
              </v-detail>

              <v-detail :label="$t('CATEGORY')" v-model="ug.object.genre_name" field="genre_name"
                        editable type="select" :options="genresOptions"
                        :filter="{genre_id: ug.object.genre_id}"/>
              <v-detail-warning v-if="editMode">
                  <html-with-links tag="p" :html="$t('APP_CATEGORY_EDIT_WARNING')" />
              </v-detail-warning>
              <v-detail :label="$t('UPDATED')" :value="ug.object.updated_at">
                {{ new Date(ug.object.updated_at).toLocaleDateString() }}
              </v-detail>
              <v-detail :label="$t('VERSION')" :value="ug.object.version"/>
              <v-detail :label="$t('SIZE')" :value="ug.object.size_hr"/>

              <v-detail v-if="ug.object.use_jit_support" :label="$t('SUPPORTS_JIT')" :value="$t('YES')"/>

              <v-detail :label="$t('OS_VERSION', {os: 'iOS'})" v-model="ug.object.min_ios_version"
                        field="min_ios_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', { os: `iOS ${ug.object.min_ios_version}` }) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'iPadOS'})"
                        v-model="ug.object.min_ipados_version" field="min_ipados_version" editable>
                {{
                  $t('REQUIRES_MIN_OS_VERSION', { os: `iPadOS ${ug.object.min_ipados_version}` })
                }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'tvOS'})" v-model="ug.object.min_tvos_version"
                        field="min_tvos_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', { os: `tvOS ${ug.object.min_tvos_version}` }) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'macOS'})"
                        v-model="ug.object.min_macos_version" field="min_macos_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', { os: `macOS ${ug.object.min_macos_version}` }) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'watchOS'})"
                        v-model="ug.object.min_watchos_version" field="min_watchos_version"
                        editable>
                {{
                  $t('REQUIRES_MIN_OS_VERSION', { os: `watchOS ${ug.object.min_watchos_version}` })
                }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'visionOS'})"
                        v-model="ug.object.min_visionos_version" field="min_visionos_version"
                        editable>
                {{
                  $t('REQUIRES_MIN_OS_VERSION', { os: `visionOS ${ug.object.min_visionos_version}` })
                }}
              </v-detail>

              <v-legal-text-detail
                :label="$t('LICENSE')"
                field="license_text"
                v-model="ug.object.license_text"
                :default-value="defaults.user_app_license_md"
                :use-default="ug.object.use_default_license"
                @update:use-default="ug.object.use_default_license = $event"
              />

              <v-legal-text-detail
                :label="$t('TOS')"
                field="tos_text"
                v-model="ug.object.tos_text"
                :default-value="defaults.user_app_tos_md"
                :use-default="ug.object.use_default_tos"
                @update:use-default="ug.object.use_default_tos = $event"
              />

              <v-legal-text-detail
                :label="$t('PRIVACY_POLICY')"
                field="privacy_policy_text"
                v-model="ug.object.privacy_policy_text"
                :default-value="defaults.user_app_privacy_policy_md"
                :use-default="ug.object.use_default_privacy_policy"
                @update:use-default="ug.object.use_default_privacy_policy = $event"
              />

              <v-detail :label="$t('SOURCE')" v-model="ug.object.source_name" field="source_name"
                        editable filterable/>

              <v-detail :label="$t('PRODUCTS')" :value="products.length">
                <ul class="in-app-products-list">
                  <li
                    v-for="product in products"
                    class="in-app-products-list__item"
                    :key="product.identifier"
                  >
                    <button class="g-pseudo-link" @click.prevent="$modal.show('in-app-product', {data: product})">
                      {{ $jt(product.name_json) }}
                    </button>
                  </li>
                </ul>

                <in-app-product-modal />
              </v-detail>
            </table>
          </div>
          <template v-if="isDeviceLinked">
            <div
              v-if="editMode || isPreview"
              class="actions"
              v-sticky
              sticky-side="bottom"
            >
              <div class="actions__buttons">
                <v-button
                  type="primary"
                  size="lg"
                  :loading="savingChanges"
                  @click.native="saveChanges"
                >
                  {{ $t('SAVE_CHANGES') }}
                </v-button>

                <v-button
                  type="primary"
                  size="lg"
                  :loading="fillingFromAppStore"
                  @click.native="fillFromAppStore"
                >
                  {{ $t('FILL_FROM_APPLE_APP_STORE') }}
                </v-button>

                <v-button
                  type="primary"
                  size="lg"
                  :loading="fillingFromUniversalSearch"
                  @click.native="$modal.show('fillDetailsFromUniversalSearch')"
                >
                  {{ $t('FILL_FROM_ANOTHER_APP') }}
                </v-button>
                <universal-search-modal @app-chosen="fillFromUniversalSearch"/>

                <v-button
                  type="negative"
                  size="lg"
                  @click.native="resetChanges"
                >
                  {{ $t('RESET_CHANGES') }}
                </v-button>

                <v-button
                  v-if="isPreview"
                  size="lg"
                  @click.native="endPreview"
                >
                  {{ $t('END_PREVIEW') }}
                </v-button>
                <v-button
                  v-else
                  size="lg"
                  @click.native="startPreview"
                >
                  {{ $t('PREVIEW') }}
                </v-button>
              </div>
            </div>
            <div
              v-else
              class="actions actions_center"
              v-sticky
              sticky-side="bottom"
            >
              <div class="actions__buttons">
                <v-button
                  size="lg"
                  @click.native="share"
                >
                  {{ $t('SHARE') }}
                </v-button>

                <v-button
                  v-if="ug.object.can_be_imported_to_library"
                  size="lg"
                  :loading="isImporting"
                  @click.native="importToLibrary"
                >
                  {{ $t('IMPORT') }}
                </v-button>

                <template v-if="!getDownloadFailureReason(ug)">
                  <v-button
                    v-if="isInternalDownload(ug)"
                    size="lg"
                    :loading="isStartingInternalDownload"
                    @click.native="startInternalDownload(ug)"
                  >
                    {{ $t('DOWNLOAD') }}
                  </v-button>
                  <v-button
                    v-else
                    tag="router-link"
                    :to="getExternalDownloadLink(ug)"
                    :target="'_blank'"
                    size="lg"
                  >
                    {{ $t('DOWNLOAD') }}
                  </v-button>
                </template>

                <v-button
                  v-if="!installationFailureReason"
                  type="primary"
                  size="lg"
                  :loading="isInstalling"
                  @click.native="install"
                >
                  {{ $t('INSTALL') }}
                </v-button>

                <v-button
                  v-if="isBuyButtonVisible"
                  type="primary"
                  size="lg"
                  :loading="isBuying"
                  @click.native="buy"
                >
                  {{ $t('BUY_FOR_PRICE', {price: formatCurrency(ug.object.price_cents_eur)}) }}
                </v-button>

                <sharing-modal/>
              </div>
              <div v-if="ug.object.detected_malware" class="actions__notice actions__notice_danger">
                <p>{{ $t('MALWARE_DETECTED_NOTICE') }}</p>
                <p v-for="malware of ug.object.detected_malware" :key="malware.name">
                  {{ malware.name }}, {{ malware.type }}. {{ $t('LEVEL') }}:&nbsp;{{ malware.level }}.
                  {{ $t('DANGER') }}:&nbsp;{{ malware.danger }}
                </p>
              </div>
              <div v-else-if="ug.object.type === 'repo_app'" class="actions__notice">
                <p>{{ $t('REPO_APP_MALWARE_NOTICE') }}</p>
              </div>
            </div>
          </template>
        </div>
      </div>
    </template>
  </section>
</template>

<script>
import Loader from '@/components/Loader';
import detailsViewMixin from '@/mixins/detailsView.js';
import installApp from '@/mixins/installApp';
import VButton from '@/components/Button.vue';
import { searchITunesUrl } from '@/utils/searchITunes';
import { cloneDeep } from 'lodash';
import VDraggable from 'vuedraggable';
import Sticky from 'vue-sticky-directive';
import { mapGetters, mapState } from 'vuex';
import VTextarea from '@/components/VTextarea.vue';
import VInput from '@/components/VInput.vue';
import BackLink from '@/components/BackLink.vue';
import { operatingSystems } from '@/data';
import SharingModal from '@/components/modals/SharePageModal.vue';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';
import formatCurrency from '@/utils/formatCurrency';
import downloadApp from '@/mixins/downloadApp';
import EditableMarkdown from './components/EditableMarkdown.vue';
import EditableLine from './components/EditableLine.vue';
import VDetail from './components/VDetail.vue';
import VLegalTextDetail from './components/VLegalTextDetail.vue';
import VDetailWarning from './components/VDetailWarning.vue';
import UniversalSearchModal from './components/UniversalSearchModal.vue';
import InAppProductModal from './components/InAppProductModal.vue';

export default {
  mixins: [detailsViewMixin, installApp, downloadApp],
  directives: { Sticky },
  components: {
    Loader,
    EditableMarkdown,
    EditableLine,
    VDetail,
    VLegalTextDetail,
    VDetailWarning,
    VButton,
    UniversalSearchModal,
    VDraggable,
    VTextarea,
    VInput,
    BackLink,
    SharingModal,
    HtmlWithLinks,
    InAppProductModal,
  },
  data() {
    return {
      isDescExpanded: false,
      screenshotsOS: '',
      isChangelogExpanded: false,
      isLoaded: false,
      isInstalling: false,
      linkToInstallError: false,
      isBuying: false,
      ug: {
        object: {
          screenshots_uris_by_os_type: {},
        },
      },
      ugCopy: {},
      iconFailedToLoad: false,
      savingChanges: false,
      screenshotsInput: null,
      uploadingScreenshots: false,
      fillingFromAppStore: false,
      fillingFromUniversalSearch: false,
      primitiveEditableKeys: ['name', 'is_18plus', 'lead', 'description', 'whatsnew', 'source_name', 'developer_name', 'genre_name', 'min_ios_version', 'min_ipados_version', 'min_tvos_version', 'min_macos_version', 'min_watchos_version', 'min_visionos_version', 'website_uri', 'license_text', 'use_default_license', 'tos_text', 'use_default_tos', 'privacy_policy_text', 'use_default_privacy_policy', 'price_cents', 'version', 'size_hr', 'updated_at'],
      isImporting: false,
      defaults: {
        user_app_license_md: '',
        user_app_privacy_policy_md: '',
        user_app_tos_md: '',
      },
      products: [],
    };
  },
  computed: {
    ...mapState(['genres']),
    ...mapGetters(['isDeviceLinked', 'isStandaloneApp', 'standaloneAppId', 'canInstallApps']),
    title() {
      return this.ug.object.name;
    },
    universalObjectId() {
      if (this.isStandaloneApp) {
        return this.standaloneAppId;
      }

      return this.$route.params.id;
    },
    id() {
      return this.$route.query.id;
    },
    hasIcon() {
      return this.ug.object.icon_uri && !this.iconFailedToLoad;
    },
    areScreenshotsVisible() {
      if (!this.ug.object.screenshots_uris_by_os_type) {
        return false;
      }

      const allScreenshots = Object.values(this.ug.object.screenshots_uris_by_os_type);

      return Boolean(allScreenshots.length);
    },
    screenshotsOSs() {
      if (this.editMode) {
        return ['universal', 'ios', 'ipados', 'macos', 'tvos', 'visionos'];
      }

      if (!this.ug.object.screenshots_uris_by_os_type) {
        return [];
      }

      return Object.entries(this.ug.object.screenshots_uris_by_os_type)
        .filter(([, uris]) => uris?.length)
        .map(([os]) => os);
    },
    devicesDictionary() {
      const entries = operatingSystems.map(({
        name,
        device,
      }) => [name, device]);
      entries.push(['universal', this.$t('UNIVERSAL_SCREENSHOTS_TITLE')]);

      return Object.fromEntries(entries);
    },
    genresOptions() {
      return Object.values(this.genres.official)
        .map(({ name }) => ({
          label: name,
          value: name,
        }));
    },
    installationFailureReason() {
      if (this.ug.no_installation_ticket_failure_reason) {
        return this.ug.no_installation_ticket_failure_reason.translated;
      }

      // if (!this.canInstallApps) {
      //   return this.$t('TO_INSTALL_VISIT_FROM_DEVICE', { device: this.config.nice_idevice_model });
      // }

      return null;
    },
    isBuyButtonVisible() {
      return this.ug.no_installation_ticket_failure_reason?.code === 'ERROR_PURCHASE_THIS_APP_FIRST_DOWNLOAD'
        || this.ug.no_installation_ticket_failure_reason?.code === 'ERROR_PURCHASE_THIS_APP_FIRST';
    },
  },
  methods: {
    formatCurrency,
    beautifyDevice(device) {
      return this.devicesDictionary[device] || device;
    },
    async handleTriggers() {
      await this.$nextTick();

      const descWrapper = this.$refs.desc && this.$refs.desc.getBoundingClientRect().bottom;
      const descText = this.$refs.desc__inner
        && this.$refs.desc__inner.getBoundingClientRect().bottom;

      if (descText <= descWrapper) {
        this.isDescExpanded = true;
      }

      if (this.$refs.changelog__inner
        && this.$refs.changelog__inner.$el.getBoundingClientRect().height <= 85) {
        this.isChangelogExpanded = true;
      }
    },
    async getInfo() {
      const response = await this.$api.universalGateway({
        universalObjectId: this.universalObjectId,
        id: this.id,
        silent: true,
      });

      if (!response.success) {
        const message = response.errors.map(error => error.translated)
          .join('\n');
        this.$router.replace({
          name: 'error',
          params: {
            message,
            link: '/',
            linkText: 'GO_TO_MAIN_PAGE',
          },
        });
        return;
      }

      this.ug = response.data;
      this.ugCopy = cloneDeep(response.data);

      this.isLoaded = true;
    },
    async getProducts() {
      const requests = this.ug.object.product_identifiers.map(identifier => this.$api.getProductMetadata({ identifier }));
      this.products = (await Promise.all(requests)).flat();
    },
    async load() {
      this.isLoaded = false;
      try {
        await this.getInfo();
        await this.getProducts();
      } finally {
        this.isLoaded = true;
      }
    },
    async fillFromAppStore() {
      const url = await this.$prompt('Apple App Store URL');

      this.fillingFromAppStore = true;

      const app = await searchITunesUrl(url);
      this.fillingFromAppStore = false;

      if (!app) {
        this.$notify({
          type: 'error',
          text: this.$t('APP_NOT_FOUND'),
        });
        return;
      }

      this.ug.object.description = app.description;
      this.ug.object.whatsnew = app.releaseNotes;
      this.ug.object.developer_name = app.artistName;
      this.ug.object.genre_name = app.primaryGenreName;
    },
    async fillFromUniversalSearch(preview) {
      this.$modal.hide('fillDetailsFromUniversalSearch');
      this.fillingFromUniversalSearch = true;

      try {
        const app = await this.$api.universalGateway({
          universalObjectId: preview.universal_object_identifier,
          id: preview.id,
        });

        const keysToCopy = [...this.primitiveEditableKeys, 'screenshots_uris_by_os_type'];

        for (const key of keysToCopy) {
          this.ug.object[key] = app.object[key];
        }
      } finally {
        this.fillingFromUniversalSearch = false;
      }
    },
    async openScreenshotsFileDialog() {
      if (!this.screenshotsInput) {
        const input = document.createElement('input');
        input.type = 'file';
        input.multiple = true;
        input.accept = 'image/png';
        input.hidden = true;

        input.addEventListener('change', async () => {
          this.uploadingScreenshots = true;
          try {
            const os = this.screenshotsOS;
            const uploads = Array.from(input.files)
              .map(file => this.$api.uploadFile({
                type: 'screenshot',
                file,
              }));
            const urls = await Promise.all(uploads);

            if (!this.ug.object.screenshots_uris_by_os_type) {
              this.$set(this.ug.object, 'screenshots_uris_by_os_type', {});
            }

            if (!this.ug.object.screenshots_uris_by_os_type[os]) {
              this.$set(this.ug.object.screenshots_uris_by_os_type, os, []);
            }

            this.ug.object.screenshots_uris_by_os_type[os].push(...urls);
          } finally {
            this.uploadingScreenshots = false;
          }

          input.value = null;
        });

        this.screenshotsInput = input;
        document.body.appendChild(input);
      }

      this.screenshotsInput.click();
    },
    removeScreenshot(os, idx) {
      this.ug.object.screenshots_uris_by_os_type[os].splice(idx, 1);
    },
    async saveChanges() {
      this.savingChanges = true;
      this.setFieldWithError('');

      const keysToSave = ['id', 'is_public', 'use_sideloading_patch', 'use_jit_support', 'use_explicit_bundle_id', 'use_group_patch', ...this.primitiveEditableKeys];
      const metadata = Object.fromEntries(Object.entries(this.ug.object)
        .filter(([key]) => keysToSave.includes(key)));
      if (this.ug.object.screenshots_uris_by_os_type) {
        metadata.screenshots_uris_by_os_type = JSON.stringify(this.ug.object.screenshots_uris_by_os_type);
      }

      try {
        const response = await this.$api.editIpaMetadata(metadata);
        if (response.success) {
          this.$notify({
            type: 'success',
            text: this.$t('DETAILS_CHANGES_SAVED_NOTIFICATION'),
          });
          this.startPreview();
        } else {
          this.$notify({
            type: 'error',
            text: response.errors.map(error => error.translated)
              .join('\n'),
          });

          if (response.errors.some(error => error.code === 'ERROR_INCOMPLETE_METADATA')) {
            this.setFieldWithError(response.data);
          }
        }
      } finally {
        this.savingChanges = false;
      }
    },
    async resetChanges() {
      await this.$confirm(this.$t('RESET_DETAILS_CHANGES_CONFIRMATION'));
      this.ug = cloneDeep(this.ugCopy);
    },
    handle18PlusContent() {
      if (this.ug.object.is_18plus !== 1 || this.editMode) {
        return;
      }

      this.$confirm(this.$t('AGE_RESTRICTED_CONTENT_ALERT'), {
        hidePage: true,
        closeManually: true,
        okText: 'GO BACK',
        cancelText: 'IM_18_PLUS',
      })
        .then(() => {
          if (history.length > 1) {
            this.$router.back();
            return;
          }

          this.$router.push('/');
        })
        .finally(() => {
          this.$modal.hide('dialog');
        });
    },
    async install() {
      this.isInstalling = true;
      try {
        await this.installApp(this.ug.object.id, 'universal', { installationTicket: this.ug.installation_ticket });
      } finally {
        this.isInstalling = false;
      }
    },
    async buy() {
      if (this.isBuying) {
        return;
      }

      this.isBuying = true;
      try {
        const response = await this.$api.initPurchase({ productIdentifier: this.ug.object.app_product_identifier });
        location.href = response.redirect_uri;
      } finally {
        this.isBuying = false;
      }
    },
    async importToLibrary() {
      await this.$confirm(this.$t('APP_IMPORT_TO_LIBRARY_CONFIRMATION'), { okText: this.$t('IMPORT') });
      this.isImporting = true;
      try {
        await this.$api.importToLibrary(this.ug.object.universal_object_identifier);
        this.$notify({
          type: 'success',
          text: this.$t('APP_IMPORT_TO_LIBRARY_SUCCESS'),
        });
      } finally {
        this.isImporting = false;
      }
    },
    async getDefaults() {
      this.defaults = await this.$api.getDefaults();
    },
    async share() {
      this.$modal.show('sharePage');
    },
    async fixStickyDirective() {
      await this.$nextTick();
      window.dispatchEvent(new Event('resize'));
    },
    handleLegalUrls() {
      const { query } = this.$route;
      if ('tos' in query && this.ug.object.tos_text) {
        this.$modal.show('tos_text');
      } else if ('privacyPolicy' in query && this.ug.object.privacy_policy_text) {
        this.$modal.show('privacy_policy_text');
      } else if ('license' in query && this.ug.object.license_text) {
        this.$modal.show('license_text');
      }
    },
    handleProductUrls() {
      const { query } = this.$route;
      if (query.product) {
        const product = this.products.find(({ identifier }) => identifier === query.product);
        if (product) {
          this.$modal.show('in-app-product', { data: product });
        }
      }
    },
    handleUrls() {
      this.handleLegalUrls();
      this.handleProductUrls();
    },
  },
  watch: {
    isLoaded(newVal) {
      if (newVal) {
        this.fixStickyDirective();
        this.handleTriggers();
        [this.screenshotsOS] = this.screenshotsOSs;
      }
    },
    isPreview() {
      this.handleTriggers();
      this.fixStickyDirective();
    },
    editMode: {
      handler(newVal) {
        if (newVal) {
          this.getDefaults();
        }
      },
      immediate: true,
    },
    ug: {
      handler() {
        this.handle18PlusContent();
      },
      deep: true,
    },
    '$route.params.id': function () {
      this.load();
    },
  },
  created() {
    this.load().then(this.handleUrls);
  },
};
</script>

